<template>
  <div class="content-setting">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.lanmu" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="region">
        <el-select v-model="ruleForm.region" placeholder="">
          <el-option label="//" value="jsu"></el-option>
          <el-option label="//" value="jxi"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-radio v-model="radio" label="1"></el-radio>
        <el-radio v-model="radio" label="2"></el-radio>
      </el-form-item>
      <el-form-item label="" prop="date1">
        <el-date-picker v-model="ruleForm.date1" type="date" placeholder="">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="" prop="date2">
        <el-time-select v-model="ruleForm.date2" :picker-options="{
          start: '08:30',
          step: '00:15',
          end: '18:30'
        }" placeholder="">
        </el-time-select>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-checkbox>1</el-checkbox>
        <el-checkbox>2</el-checkbox>
        <el-checkbox>3</el-checkbox>
        <el-checkbox>4</el-checkbox>
      </el-form-item>
      <el-form-item label="">
        <el-rate v-model="value1"></el-rate>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
      </el-form-item>
      <el-form-item label="">
        <el-input type="textarea" autosize placeholder="" v-model="textarea1">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        name: '',
        lanmu: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        type: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ],
        resource: [
          { required: true, message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="less" scoped>
.content-setting {
  width: 100%;
  height: 100%;
  padding-top: 15px;
}
/deep/.el-form-item__label {
    font-size: 9px;
    line-height: 20px;
}
/deep/.el-input__inner {
    height: 23px;
    line-height: 23px;
    font-size: 10px;
    width: 96%;
  }
/deep/.el-form-item__content {
    line-height: 20px;
  }
  /deep/.el-input__icon {
    line-height: 20px;
}
/deep/.el-button{
  padding: 6px 5px;
  font-size: 10px;
}
/deep/.el-checkbox__label {
    padding-left: 2px;
    font-size: 10px;
}
/deep/.el-radio__label {
  font-size: 10px;
    padding-left: 3px;
}
/deep/.el-textarea__inner {
    padding: 5px 5px;
    font-size: 10px;
    width: 96%;

    }
</style>